import * as React from "react"
import * as ReactDOM from "react-dom"
import { Remote } from "../Remote/Remote"
import { TreeView } from '@progress/kendo-react-treeview';
import { Splitter } from '@progress/kendo-react-layout';
import { ContextMenu } from "../ContextMenu/ContextMenu";
import { KendoDialog } from "../Dialog/Dialog";
import { Upload, UploadOnStatusChangeEvent, UploadFileStatus } from '@progress/kendo-react-upload';
import { Input } from "@progress/kendo-react-inputs";
import { Button } from "@progress/kendo-react-buttons";
declare var window: any;
declare var $: any;

interface IDocumentManagerProps {
    rootFolderName: string,
    parent: any;
    basePath: string;
    apiFolderPath: string;
    apiFilePath: string;
    uploadUrl: string;
    operationConfirmationUrl?: string;
    operationUrl?: string;
}

interface IDocumentManagerState {
    treeNodes: any[];
    files: any[];
    panes: any[];
    contextMenu: any;
    copiedElement: any;
    cutElement: any;
    paneContextPath: string;
    contextPath: string;
    dialogProps: any;
    showCreateFolderDialog: boolean;
    showProgressBar: boolean;
    uploadingFiles: any[];
    contextMenuTopMargin: number,
    contextMenuLeftMargin: number
}

export class DocumentManager extends React.Component<IDocumentManagerProps, IDocumentManagerState> {
    static instance: DocumentManager;
    dialog: any;
    treeNodeParents: any[];
    uploader: Upload;
    searchField: Input;

    constructor(props: any) {
        super(props);

        this.onLayoutChange = this.onLayoutChange.bind(this);
        this.onInit = this.onInit.bind(this);
        this.deepMap = this.deepMap.bind(this);
        this.onTreeExpandChange = this.onTreeExpandChange.bind(this);
        this.onTreeItemClick = this.onTreeItemClick.bind(this);
        this.folderAndFileMap = this.folderAndFileMap.bind(this);
        this.iconClassName = this.iconClassName.bind(this);
        this.onContextMenu = this.onContextMenu.bind(this);
        this.renderContextMenu = this.renderContextMenu.bind(this);
        this.onCopy = this.onCopy.bind(this);
        this.onContextMenuMouseOut = this.onContextMenuMouseOut.bind(this);
        this.handleFileContextMenu = this.handleFileContextMenu.bind(this);
        this.onPaste = this.onPaste.bind(this);
        this.onDelete = this.onDelete.bind(this);
        this.onRename = this.onRename.bind(this);
        this.handlePaneContextMenu = this.handlePaneContextMenu.bind(this);
        this.registerDialog = this.registerDialog.bind(this);
        this.onCreateFolder = this.onCreateFolder.bind(this);
        this.loadFileOnPane = this.loadFileOnPane.bind(this);
        this.onDeleteFolder = this.onDeleteFolder.bind(this);
        this.onRenameFolder = this.onRenameFolder.bind(this);
        this.onTreeContextMenu = this.onTreeContextMenu.bind(this);
        this.onFolderDoubleClick = this.onFolderDoubleClick.bind(this);
        this.handleFileDoubleClick = this.handleFileDoubleClick.bind(this);
        this.handleFolderDoubleClick = this.handleFolderDoubleClick.bind(this);
        this.deepScan = this.deepScan.bind(this);
        this.expandAllParents = this.expandAllParents.bind(this);
        this.deepSearch = this.deepSearch.bind(this);
        this.onDragging = this.onDragging.bind(this);
        this.onDraggingComplete = this.onDraggingComplete.bind(this);
        this.onCut = this.onCut.bind(this);
        this.onDeleteFile = this.onDeleteFile.bind(this);
        this.onRenameFile = this.onRenameFile.bind(this);
        this.onDocumentSearch = this.onDocumentSearch.bind(this);
        this.onDocumentSearchPressed = this.onDocumentSearchPressed.bind(this);
        this.onDownloadDocument = this.onDownloadDocument.bind(this);
        this.onUploadProgress = this.onUploadProgress.bind(this);
        this.onUploadFileAdded = this.onUploadFileAdded.bind(this);
        this.onCreateFolderButtonClick = this.onCreateFolderButtonClick.bind(this);
        this.urlencode = this.urlencode.bind(this);
        this.onDownloadDocumentClick = this.onDownloadDocumentClick.bind(this);
        this.onBreadcrumClick = this.onBreadcrumClick.bind(this);

        DocumentManager.instance = this;

        let dialogProps = {
            actionText: '',
            dialogTitle: '',
            buttons: [{}],
            dialogHeight: '50%',
            dialogWidth: '50%',
            getUrl: '',
            method: '',
            postUrl: '',
            uniqueKey: '',
            visible: false,
            register: this.registerDialog
        }

        this.state = {
            treeNodes: [],
            files: [],
            panes: [
                { size: '30%', resizable: true },
                { resizable: true }
            ],
            contextMenu: null,
            copiedElement: null,
            paneContextPath: this.props.basePath,
            contextPath: this.props.basePath,
            showCreateFolderDialog: false,
            dialogProps: dialogProps,
            showProgressBar: false,
            cutElement: null,
            uploadingFiles: [],
            contextMenuLeftMargin: 25,
            contextMenuTopMargin: 300
        };

        this.onInit();
    }

    onInit() {
        let sender = this;
        Remote.get(this.props.apiFolderPath + "?id=" + this.props.basePath, (data: any[]) => {
            let collect: any[] = this.deepMap(data, sender);
            let withRoot: any[] = [];
            withRoot.push({
                text: this.props.rootFolderName,
                icon: 'folder',
                relativePath: this.props.basePath,
                type: "folder",
                expanded: true,
                items: collect
            });
            let selectedPath = this.state.paneContextPath || this.props.basePath;
            sender.setState({ treeNodes: withRoot, paneContextPath: selectedPath }, sender.loadFileOnPane);
        }, (data: any) => { window.Dialog.alert(data); })
    }

    deepMap(arr: any[], sender: any) {
        let a: any[] = [];
        if (arr == undefined)
            return a;
        arr.map((d: any, i: number) => {
            a.push({
                text: d.name,
                icon: 'folder',
                relativePath: d.relativePath,
                type: d.type,
                expanded: false,
                items: sender.deepMap(d.subFolders, sender)
            });
        });
        return a;
    }

    deepScan(arr: any[], relativePath: any, parent: any) {
        arr.map((item: any, index: number) => {
            item.parent = parent;
            if (item.relativePath == relativePath) {
                item.expanded = true;
                item.selected = true;
                this.expandAllParents(item);
            }
            else {
                item.selected = false;
                item.expanded = false;
            }
            this.deepScan(item.items, relativePath, item);
        })
    }
    
    deepSearch(arr: any[], relativePath: string): any {
        for (let i in arr) {
            if (arr[i].relativePath == relativePath)
                return arr[i];
            else {
                let item = this.deepSearch(arr[i].items, relativePath);
                if (item != undefined) {
                    return item;
                }
            }
        }
        
    }

    expandAllParents(item: any) {
        if (item.parent != null) {
            item.parent.expanded = true;
            this.expandAllParents(item.parent);
        }
    }

    folderAndFileMap(arr: any[], sender: any) {
        let a: any[] = [];
        if (arr == undefined)
            return a;
        arr.map((d: any, i: number) => {
            a.push({
                text: d.name,
                icon: sender.getIcon(d),
                relativePath: d.relativePath,
                type: d.type,
                expanded: false,
                items: []
            });
        });
    }
    
    onLayoutChange(e: any) {
        this.setState({
            panes: e
        });
    }


    onTreeExpandChange(e: any) {
        e.item.selected = !e.item.selected;
        this.forceUpdate();
    }


    onTreeItemClick(e: any) {
        e.item.expanded = !e.item.expanded;
        this.setState({ paneContextPath: e.item.relativePath }, this.loadFileOnPane)
        this.forceUpdate();
    }

    loadFileOnPane() {
        let sender = this;
        Remote.get(this.props.apiFilePath + "?id=" + this.state.paneContextPath, (data: any[]) => {
            let collect: any[] = this.deepMap(data, sender);
            sender.setState({ files: collect });
            sender.updateTreeviewSelection(this.state.paneContextPath);
        }, (data: any) => { window.Dialog.alert(data); })
    }

    updateTreeviewSelection(relativePath: string) {
        let arr = this.state.treeNodes;
        this.deepScan(arr, relativePath, null)
        this.setState({ treeNodes: arr }, this.forceUpdate);
    }

    renderFile(v: any, index: number) {
        return (<button key={index} itemID={index+""} className="col-lg-2 col-md-3 col-sm-6 text-center explorer-list" onClick={this.onDownloadDocumentClick} onContextMenu={this.onContextMenu}>
        <div className="explorer-button">
                <span className={this.iconClassName(v.text)}></span>
                <small>{v.text}</small>
                </div>
        </button>);
    }

    renderFolder(v: any, index: number) {
        return (<button key={index} itemID={index + ""} className="col-lg-2 col-md-3 col-sm-6 text-center explorer-list" onContextMenu={this.onContextMenu} onDoubleClick={this.onFolderDoubleClick}>
                <div className="explorer-button">
                <span className="k-icon k-i-folder"></span>
                <small>{v.text}</small>
                </div>
        </button>);
    }

    renderContextMenu() {
        return this.state.contextMenu;
    }

    onFolderDoubleClick(e: any) {
        e.preventDefault();
        e.stopPropagation();
        var index = parseInt(e.currentTarget.getAttribute("itemid"));
        var item = this.state.files[index];
        if (item.type == 'file')
            this.handleFileDoubleClick(e, item)
        else if (item.type == 'folder') {
            this.handleFolderDoubleClick(e, item)
        }   
    }

    handleFileDoubleClick(e: any, item: any) {

    }

    handleFolderDoubleClick(e: any, item: any) {
        this.setState({ paneContextPath: item.relativePath}, this.loadFileOnPane)
    }

    renderFilesAndFolders() {
        let items: any[] = [];
        this.state.files.map((item: any, index: number) => {
            if (item.type == 'file') {
                items.push(this.renderFile(item, index));
            }
            else if (item.type == 'folder') {
                items.push(this.renderFolder(item, index))
            }
        });
        return items;
    }

    is = (fileName: string, ext:string) => new RegExp(`.${ext}\$`).test(fileName);

    iconClassName(text: any) {
        if (this.is(text, 'pdf')) {
            ``
            return 'k-icon k-i-file-pdf';
        } else if (this.is(text, 'html')) {
            return 'k-icon k-i-html';
        } else if (this.is(text, 'jpg|png|bmp|gif|tif')) {
            return 'k-icon k-i-image';
        } else if (this.is(text, 'doc|docx')) {
            return 'k-icon k-i-file-doc';
        } else if (this.is(text, 'xls|xlsx')) {
            return 'k-icon k-i-file-xls';
        } else if (this.is(text, 'ppt|pptx')) {
            return 'k-icon k-i-file-ppt';
        }
        else {
            return 'k-icon k-i-file-txt';
        }
    }

    onCut(e: any, item: any) {
        this.setState({ cutElement: item, contextMenu: null, copiedElement: null }, this.forceUpdate);
    }

    onCopy(e: any, item: any) {
        this.setState({ copiedElement: item, contextMenu: null }, this.forceUpdate)
    }

    onPaste(e: any, item: any) {
        let sender = this;
        if (this.state.cutElement) {
            let data = {
                source: this.state.cutElement.relativePath,
                path: item && item.relativePath || this.state.paneContextPath,
                operation: 'FileCutPaste'
            }
            Remote.postData(this.props.operationUrl, data, (result: any) => { sender.loadFileOnPane() }, (err: any) => {  });
        }
        else if (this.state.copiedElement && this.state.copiedElement.type == 'file') {
            let data = {
                source: this.state.copiedElement.relativePath,
                path: item && item.relativePath || this.state.paneContextPath,
                operation: 'FileCopyPaste'
            }
            Remote.postData(this.props.operationUrl, data, (result: any) => { sender.loadFileOnPane() }, (err: any) => { });
        }
        else if (this.state.copiedElement && this.state.copiedElement.type == 'folder') {
            let data = {
                source: this.state.copiedElement.relativePath,
                path: item && item.relativePath || this.state.paneContextPath,
                operation: 'FolderCopyPaste'
            }
            Remote.postData(this.props.operationUrl, data, (result: any) => { sender.onInit() }, (err: any) => { });
        }
        this.setState({ copiedElement: null, cutElement: null, contextMenu: null }, this.forceUpdate)
    }

    onDelete(e: any, item: any) {
        if (item.type && item.type == 'folder') {
            this.setState({ contextMenu: null }, this.forceUpdate)
            this.onDeleteFolder(item);
        }
        else if (item.type && item.type == 'file') {
            this.setState({ contextMenu: null }, this.forceUpdate)
            this.onDeleteFile(item);
        }
    }

    onRename(e: any, item: any) {
        if (item.type && item.type == 'folder') {
            this.setState({ contextMenu: null }, this.forceUpdate)
            this.onRenameFolder(item);
        }
        else if (item.type && item.type == 'file') {
            this.setState({ contextMenu: null }, this.forceUpdate)
            this.onRenameFile(item);
        }
    }

    onDeleteFile(item: any) {
        let sender = this;
        let buttons = [
            { primary: true, requireValidation: true, text: "Delete", action: (e: any, o: any) => { sender.setState({ paneContextPath: this.state.paneContextPath }, sender.onInit) } },
            { primary: false, text: "Cancel", action: (e: any, o: any) => { } },
        ]

        let dialogProps = {
            ...this.state.dialogProps,
            dialogHeight: '36%',
            dialogWidth: '37%',
        }

        this.openDialog({
            dialogTitle: "Delete File",
            buttons: buttons,
            getUrl: this.props.operationConfirmationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=DeleteFile",
            method: "post",
            postUrl: this.props.operationUrl
        }, dialogProps);
    }

    onDeleteFolder(item: any) {
        let sender = this;
        let arr: string[] = item.relativePath.split("/");
        arr.pop();
        let path = arr.join("/");
        let buttons = [
            { primary: true, requireValidation: true, text: "Delete", action: (e: any, o: any) => { sender.setState({ paneContextPath: path }, sender.onInit) } },
            { primary: false, text: "Cancel", action: (e: any, o: any) => { } },
        ]

        let dialogProps = {
            ...this.state.dialogProps,
            dialogHeight: '36%',
            dialogWidth: '37%',
        }

        this.openDialog({
            dialogTitle: "Delete Folder",
            buttons: buttons,
            getUrl: this.props.operationConfirmationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=DeleteFolder",
            method: "post",
            postUrl: this.props.operationUrl
        }, dialogProps);
    }

    onRenameFolder(item: any) {
        let sender = this;
        let arr: string[] = item.relativePath.split("/");
        arr.pop();
        let path = arr.join("/");
        let buttons = [
            { primary: true, requireValidation: true, text: "Rename", action: (e: any, o: any) => { sender.setState({ paneContextPath: path }, sender.onInit) } },
            { primary: false, text: "Cancel", action: (e: any, o: any) => { } },
        ]

        let dialogProps = {
            ...this.state.dialogProps,
            dialogHeight: '25%',
            dialogWidth: '35%',
        }

        this.openDialog({
            dialogTitle: "Rename Folder",
            buttons: buttons,
            getUrl: this.props.operationConfirmationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=RenameFolder",
            method: "post",
            postUrl: this.props.operationUrl
        }, dialogProps);
    }

    onRenameFile(item: any) {
        let sender = this;
        let arr: string[] = item.relativePath.split("/");
        arr.pop();
        let path = arr.join("/");
        let buttons = [
            { primary: true, requireValidation: true, text: "Rename", action: (e: any, o: any) => { sender.setState({ paneContextPath: path }, sender.onInit) } },
            { primary: false, text: "Cancel", action: (e: any, o: any) => { } },
        ]

        let dialogProps = {
            ...this.state.dialogProps,
            dialogHeight: '25%',
            dialogWidth: '35%',
        }

        this.openDialog({
            dialogTitle: "Rename File",
            buttons: buttons,
            getUrl: this.props.operationConfirmationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=RenameFile",
            method: "post",
            postUrl: this.props.operationUrl
        }, dialogProps);
    }

    onCreateFolderButtonClick(e: any) {
        this.onCreateFolder(e, null);
    }

    onCreateFolder(e: any, item: any) {
        let sender = this;
        let buttons = [
            { primary: true, requireValidation: true, text: "Create", action: (e: any, o: any) => { sender.onInit(); } },
            { primary: false, text: "Cancel", action: (e: any, o: any) => { } },
        ]

        let dialogProps = {
            ...this.state.dialogProps,
            dialogHeight: '25%',
            dialogWidth: '35%',
        }

        if (item == null) {
            this.setState({ contextPath: this.state.paneContextPath, showCreateFolderDialog: true, dialogProps: dialogProps }, this.onInit)
            this.openDialog({
                dialogTitle: "Create Folder",
                buttons: buttons,
                getUrl: this.props.operationConfirmationUrl + "?id=" + this.state.paneContextPath + "&id1=CreateFolder",
                method: "post",
                postUrl: this.props.operationUrl
            }, dialogProps);
        }
        else {
            this.setState({ contextPath: item.relativePath, showCreateFolderDialog: true, dialogProps: dialogProps }, this.onInit)
            this.openDialog({
            dialogTitle: "Create Folder",
            buttons: buttons,
                getUrl: this.props.operationConfirmationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=CreateFolder",
            method: "post",
            postUrl: this.props.operationUrl
        }, dialogProps);
        }

    }

    onContextMenuMouseOut(e: any) {
        this.setState({ contextMenu: null }, this.forceUpdate)
    }

    onContextMenu(e: any) {
        e.preventDefault();
        e.stopPropagation();
        return;
        //var index = parseInt(e.currentTarget.getAttribute("itemid"));
        //var item = this.state.files[index];
        //if (item == undefined && isNaN(index)) {
        //    this.handlePaneContextMenu(e, item);
        //}
        //else {
        //    if (item.type == 'file')
        //        this.handleFileContextMenu(e, item)
        //    else if (item.type == 'folder') {
        //        this.handleFolderContextMenu(e, item)
        //    }
        //}
    }

    onTreeContextMenu(e: any) {
        e.preventDefault();
        e.stopPropagation();
        return;
        //var path = e.currentTarget.getAttribute("itemid");
        //var item = this.deepSearch(this.state.treeNodes,path);
        //if (item != undefined) {
        //    this.handleFolderContextMenu(e, item)
        //}
    }

    handleFileContextMenu(e: any, item: any) {
        var menus: any[] = [];
        menus.push({
            text: "Copy",
            icon: 'copy',
            action: this.onCopy
        });
        menus.push({
            text: "Cut",
            icon: 'cut',
            action: this.onCut
        });
        menus.push({
            text: "Delete",
            icon: 'delete',
            action: this.onDelete
        });
        menus.push({
            text: "Rename",
            icon: 'change-manually',
            action: this.onRename
        });
        menus.push({
            text: "Download",
            icon: 'download',
            action: this.onDownloadDocument
        });
        let style = {
            top: (e.clientY - this.state.contextMenuTopMargin) + "px",
            left: (e.clientX - this.state.contextMenuLeftMargin) + "px",
            zIndex: 1000001
        }

        
        let menu = <ContextMenu menus={menus} sender={this} style={style} item={item}></ContextMenu>
        this.setState({ contextMenu: menu });
        
    }

    onDownloadDocument(e: any, item: any) {
        let url = this.props.operationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=download"
        Remote.download(url, (data: any) => { }, (error: any) => {
            window.Dialog.alert(error);
        });
    }

    onDownloadDocumentClick(e: any) {
        let idx = e.currentTarget.getAttribute("itemid");
        var item = this.state.files[idx];
        if(item != undefined){
            let url = this.props.operationUrl + "?id=" + this.urlencode(item.relativePath) + "&id1=download"
            Remote.download(url, (data: any) => { }, (error: any) => {
                window.Dialog.alert(error);
            });
        }
        
    }

    handleFolderContextMenu(e: any, item: any) {
        var menus: any[] = [];
        menus.push({
            text: "Folder",
            icon: 'folder',
            action: this.onCreateFolder
        });
        if (item.relativePath != this.props.basePath) {
            menus.push({
                text: "Copy",
                icon: 'copy',
                action: this.onCopy
            });
        }
        if (this.state.copiedElement != null || this.state.cutElement != null) {
            menus.push({
                text: "Paste",
                icon: 'paste',
                action: this.onPaste
            });
        }      
        if (item.relativePath != this.props.basePath) {
            menus.push({
                text: "Rename",
                icon: 'change-manually',
                action: this.onRename
            });
            menus.push({
                text: "Delete",
                icon: 'delete',
                action: this.onDelete
            });
        }
        

        let style = {
            top: (e.clientY - this.state.contextMenuTopMargin) + "px",
            left: (e.clientX - this.state.contextMenuLeftMargin) + "px",
            zIndex: 1000001
        }
        if (menus.length > 0) {
            let menu = <ContextMenu menus={menus} sender={this} style={style} item={item}></ContextMenu>
            this.setState({ contextMenu: menu });
        }
    }

    handlePaneContextMenu(e: any, item: any) {
        var menus: any[] = [];
        menus.push({
            text: "Folder",
            icon: 'folder',
            action: this.onCreateFolder
        });
        if (this.state.copiedElement != null || this.state.cutElement != null) {
            menus.push({
                text: "Paste",
                icon: 'paste',
                action: this.onPaste
            });
        } 

        let style = {
            top: (e.clientY - this.state.contextMenuTopMargin) + "px",
            left: (e.clientX - this.state.contextMenuLeftMargin) + "px"
        }
        if (menus.length > 0) {
            let menu = <ContextMenu menus={menus} sender={this} style={style} item={item}></ContextMenu>
            this.setState({ contextMenu: menu });
        }
    }
    
    registerDialog(e: any) {
        DocumentManager.instance.dialog = e;
    }

    renderProgressing() {
        return (<div className="k-loading-mask">
            <span className="k-loading-text">Loading</span>
            <div className="k-loading-image"></div>
            <div className="k-loading-color"></div>
        </div>)
    }

    onDragging(e: any) {
        let sender = this;
        e.preventDefault()
        e.stopPropagation();
        $(".document-manager").eq(0).find(".k-upload").eq(1).css({ display: 'block', height: this.props.parent.clientHeight + "px" });
        $(".k-upload").eq(1).on("dragleave", function (e1: any) {
            sender.onDraggingComplete(e1)
        })
        return false;
    }

    onDraggingComplete(e: any) {
        $(".document-manager").eq(0).find(".k-upload").eq(1).css({ display: 'none' });
        return false;
    }


    openDialog(param: any, dialogProps: any) {
        DocumentManager.instance.dialog.props = {
            ...dialogProps,
            dialogTitle: param.dialogTitle,
            buttons: param.buttons,
            getUrl: param.getUrl,
            getMethod: param.getMethod || 'get',
            postData: param.postData || [],
            method: param.method,
            postUrl: param.postUrl,
        }
        this.setState({ dialogProps: DocumentManager.instance.dialog.props }, DocumentManager.instance.dialog.openForm);
    }

    onDocumentSearchPressed(e: any) {
        if (e.keyCode == 13) {
            this.onDocumentSearch(e);
        }
    }

    onDocumentSearch(e: any) {
        let sender = this;
        Remote.get(this.props.operationConfirmationUrl + "?id=" + this.props.basePath + "&id1=SearchFiles&id2=" + this.searchField.value, (data: any) => {
            let collect: any[] = this.deepMap(data, sender);
            this.setState({
                files: collect
            }, this.forceUpdate)                
        }, (err: any) => {
            window.Dialog.alert(err);
        })
    }

    componentDidMount() {
        //$(".document-manager").eq(0).find(".k-upload").eq(1).css({ position: 'absolute', height: this.props.parent.clientHeight + "px", width: '100%', display: 'none',"z-index": 100001 });
        //$(".document-manager").eq(0).find(".k-upload").eq(1).find(".k-dropzone").css({ height: '100%' });
        //$(".document-manager").eq(0).find(".k-upload").eq(1).find("span").css({ display: 'none' });
    }

    onUploadProgress(e: any) {
        this.setState({ showProgressBar: true }, this.forceUpdate)
    }

    onUploadFileAdded(e: any) {
        this.setState({
            uploadingFiles: e.newState,
            showProgressBar: true
        });
    }

    urlencode(str: string) {
        return str.replace("&", "-amp;");
    }

    onBreadcrumClick(e: any) {
        let link = e.currentTarget.getAttribute("rel")
        this.setState({ paneContextPath: link }, this.loadFileOnPane)
    }

    renderBreadCrum() {
        let sp = this.state.paneContextPath.split("/");
        let path = "";
        let output: any[] = [];
        for (let i = 1; i < sp.length; i++) {
            path += "/" + sp[i];
            let link = (<span className="dir-separator" key={i}>/<a rel={path} href="javascript:void(0)" onClick={this.onBreadcrumClick}>{sp[i]}</a></span>);
            output.push(link);
        }
        return output;
    }

    render() {
        return (
            <div className="col-sm-12 document-manager k-dropzone" onDragEnter={this.onDragging} onDrop={this.onDraggingComplete} onMouseUp={this.onDraggingComplete}>
                <div className="row mb-2 document-manager-top-header">
                    <div className="col-sm-6 pl-0">
                        <Input ref={c => { this.searchField = c; }} type="text" onKeyDown={this.onDocumentSearchPressed} className="w-50" /> <Button primary={true} onClick={this.onDocumentSearch}>Search</Button>
                    </div>
                    <div className="col-sm-4 pr-0" style={{display: 'none'}}>
                        <Button onClick={this.onCreateFolderButtonClick}>Add Folder</Button>
                        <Upload
                            batch={false}
                            multiple={true}
                            withCredentials={false}
                            showFileList={false}
                            saveUrl={this.props.uploadUrl + this.state.paneContextPath}
                            onAdd={this.onUploadFileAdded}
                            onBeforeUpload={this.onUploadFileAdded}
                            onProgress={this.onUploadProgress}
                            onStatusChange={(ev: UploadOnStatusChangeEvent) => {

                                if (ev.response.response.status == true) {
                                    this.setState({ showProgressBar: false }, this.loadFileOnPane)
                                }
                                else {
                                    this.setState({ showProgressBar: true }, this.forceUpdate)
                                }
                            }}
                        />
                    </div>
                    <div className="col-sm-6 pr-0 text-right">
                        <div className="file-path">
                        {this.renderBreadCrum()}
                        </div>
                    </div>
                </div>
                <div className="row">
                    <Splitter
                        style={{ height: 350, width: "100%" }}
                        panes={this.state.panes}
                        orientation={'horizontal'}
                        onLayoutChange={this.onLayoutChange}
                    >
                        <div className="pane-content" onContextMenu={this.onContextMenu} style={{ display: 'table', height: '100%', width: '100%' }}>
                            <TreeView                                
                                data={this.state.treeNodes}
                                expandIcons={true}
                                onExpandChange={this.onTreeExpandChange}
                                onItemClick={this.onTreeItemClick}                                
                                aria-multiselectable={true}
                                itemRender={(props) => {
                                    return (<div onContextMenu={this.onTreeContextMenu} itemID={props.item.relativePath}><span className="k-icon k-i-folder icon-font" key='0'></span> <small>{props.item.text}</small></div>)
                                    }
                                }
                            />
                        </div>
                        <div className="pane-content file-explore" onContextMenu={this.onContextMenu} style={{ display: 'table', height: '100%', width: '100%' }}>
                            <div className="row no-gutters align-items-start">
                                {this.renderFilesAndFolders()}                                
                            </div>
                        </div>
                    </Splitter>
                </div>
                {this.state.contextMenu != null && this.renderContextMenu()}
                <KendoDialog {...this.state.dialogProps}></KendoDialog>
                {this.state.showProgressBar && this.renderProgressing()}
                {/*<Upload
                    ref={(e) => { this.uploader = e; }}
                    batch={false}
                    multiple={true}
                    withCredentials={false}
                    showFileList={false}
                    saveUrl={this.props.uploadUrl + this.state.paneContextPath}
                    onAdd={this.onUploadFileAdded}
                    onBeforeUpload={this.onUploadFileAdded}
                    onProgress={this.onUploadProgress}
                    onStatusChange={(ev: UploadOnStatusChangeEvent) => {
                        if (ev.response.response.status == true) {
                            this.setState({ showProgressBar: false }, this.loadFileOnPane)
                        }
                    }}
                />*/}
            </div>
            );
    }
}