import * as React from "react";
import * as ReactDOM from "react-dom";
import { DocumentManager } from "./DocumentManager";
// import '@progress/kendo-theme-bootstrap/dist/all.css';
declare var window: any;

export function loadDocumentManager(basePath: any, rootFolderName: string) {
    ReactDOM.render(<DocumentManager basePath={basePath} rootFolderName={rootFolderName} apiFolderPath="/DocumentManager/GetFolderTree" apiFilePath="/DocumentManager/GetFilesAndFolders" uploadUrl="/DocumentManager/Upload?id=" operationConfirmationUrl="/DocumentManager/Operation" operationUrl="/DocumentManager/Operation" parent={document.getElementById("contractDocumentManagement")} />, document.getElementById("contractDocumentManagement"));
}

export function loadProjectDocumentManager(basePath: any, rootFolderName: string) {
    ReactDOM.render(<DocumentManager basePath={basePath} rootFolderName={rootFolderName} apiFolderPath="/DocumentManager/GetFolderTree" apiFilePath="/DocumentManager/GetFilesAndFolders" uploadUrl="/DocumentManager/Upload?id=" operationConfirmationUrl="/DocumentManager/Operation" operationUrl="/DocumentManager/Operation" parent={document.getElementById("projectDocumentManagement")} />, document.getElementById("projectDocumentManagement"));
}

window.admin = { documentManagement: { loadDocumentManager: loadDocumentManager, loadDocumentManagerProject: loadProjectDocumentManager } };